// Gallery.tsx
import React from 'react';
import { Row, Col, Image } from 'react-bootstrap';

const Gallery: React.FC<{ images: string[] }> = ({ images }) => (
  <Row>
    {images.map((src, i) => (
      <Col xs={6} md={4} key={i} className="mb-3">
        <Image src={src} fluid thumbnail />
      </Col>
    ))}
  </Row>
);

export default Gallery;