import React from 'react';
import { Container } from 'react-bootstrap';

interface HeroProps {
  bg: string;
  logo: string;
  motto: string;
}

const HeroBanner: React.FC<HeroProps> = ({ bg, logo, motto }) => (
  <div style={{ 
    position: 'relative',
    height: '100vh',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    overflow: 'hidden'
  }}>
    <div style={{
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundImage: `url(${process.env.PUBLIC_URL + bg})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      opacity: 0.5,
      zIndex: 1
    }} />
    <Container className="text-white text-center" style={{ zIndex: 2 }}>
      <img src={process.env.PUBLIC_URL + logo} alt="Logo" style={{ maxWidth: '150px' }} />
      <h1 className="mt-3">{motto}</h1>
    </Container>
  </div>
);

export default HeroBanner;
