// StaffCards.tsx
import React from 'react';
import { Card, Col, Row } from 'react-bootstrap';

interface Staff {
  name: string;
  role: string;
  photo: string;
}

const StaffCards: React.FC<{ staff: Staff[] }> = ({ staff }) => (
  <Row>
    {staff.map((s, i) => (
      <Col xs={12} md={4} key={i} className="mb-3">
        <Card>
          <Card.Img variant="top" src={s.photo} />
          <Card.Body>
            <Card.Title>{s.name}</Card.Title>
            <Card.Text>{s.role}</Card.Text>
          </Card.Body>
        </Card>
      </Col>
    ))}
  </Row>
);

export default StaffCards;