import React from 'react';
import config from '../config.json';
import { Container, Form, Button, Card } from 'react-bootstrap';

const Admissions: React.FC = () => {
  const formFields = config.admissions.formFields;

  return (
    <Container className="my-5">
      <h2 className="text-center mb-4">Admissions</h2>
      <Card className="p-4 shadow-sm">
        <Card.Body>
          <Card.Text className="mb-4 text-muted text-center">{config.admissions.notice}</Card.Text>
          <Form>
            {formFields.map((field, index) => (
              <Form.Group key={index} className="mb-3">
                <Form.Label>{field.label}</Form.Label>
                <Form.Control type={field.type} placeholder={field.label} />
              </Form.Group>
            ))}
            <div className="text-end">
  <Button style={{ backgroundColor: config.colors.primary, borderColor: config.colors.primary, width: 'auto' }} size="sm" type="submit">Submit Application</Button>
</div>
          </Form>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default Admissions;
